import React from 'react'
import { useEffect } from 'react';
import { useRef } from 'react';
import { useState } from 'react';
import DatePicker from "react-multi-date-picker";

const Calender = () => {
    const today = new Date()
    const tomorrow = new Date()
  
    tomorrow.setDate(tomorrow.getDate() + 1)
  
    const [values, setValues] = useState([today])
    console.log(values);
    const datePickerRef = useRef()
    const shwCalnder= () => datePickerRef.current.openCalendar()
    useEffect(()=>{
        shwCalnder()
    },[])
  return (
    <div className="w-100 d-flex justify-content-start align-items-center flex-column  " style={{height:"400px"}}>
 
    <DatePicker 
    multiple
   
    value={values} 
    onChange={setValues}
      ref={datePickerRef} 
      inputClass="custom-input"
      style={{
       paddingTop:"-60px",
        paddingRight: "65px"
      }}
      
    >
      
    </DatePicker>

    <div className='d-flex justify-content-center align-items-center'>
        <button  type="button" className="btn btn-secondary me-2" style={{marginTop:"300px"}} >Add</button>

        <button type="button" className="btn btn-secondary  " style={{marginTop:"300px"}}>Cancel</button>

        </div>
   </div>
  )
}

export default Calender